import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  IComponentCanDeactivate,
  RX_APPLICATION,
  RxBundleCacheService,
  RxComponentCanDeactivateGuard,
  RxDefinitionNameService,
  RxPageTitleService
} from '@helix/platform/shared/api';
import { IViewDesignerConfiguration, RxViewDesignerComponent } from '@helix/platform/view/designer';
import { combineLatest, Subscription } from 'rxjs';
import { RxUtilityModalsService } from '@helix/platform/ui-kit';
import { toNumber, values } from 'lodash';
import { pluck } from 'rxjs/operators';
import { RX_SHELL, RX_VIEW_DEFINITION } from '@helix/platform/view/api';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'rx-view-designer-page',
  templateUrl: './view-designer-page.component.html',
  styleUrls: ['./view-designer-page.component.scss']
})
export class ViewDesignerPageComponent implements OnInit, OnDestroy, IComponentCanDeactivate {
  configuration: IViewDesignerConfiguration;

  private subscription: Subscription;
  private isNewView: boolean;

  @ViewChild(RxViewDesignerComponent)
  private viewDesignerComponent: RxViewDesignerComponent;

  constructor(
    private activatedRoute: ActivatedRoute,
    private bundleCacheService: RxBundleCacheService,
    private definitionNameService: RxDefinitionNameService,
    private rxUtilityModalsService: RxUtilityModalsService,
    private rxPageTitleService: RxPageTitleService,
    private router: Router,
    private translateService: TranslateService,
    private rxComponentCanDeactivateGuard: RxComponentCanDeactivateGuard
  ) {}

  ngOnInit(): void {
    this.rxComponentCanDeactivateGuard.setPageComponent(this);

    this.subscription = combineLatest([
      this.activatedRoute.params,
      this.activatedRoute.queryParams.pipe(pluck('layoutTemplate'))
    ]).subscribe(([{ definitionName, bundleId }, layoutTemplate]) => {
      this.bundleCacheService.bundleId = bundleId || this.definitionNameService.getBundleId(definitionName);

      this.isNewView = !definitionName;

      this.configuration = {
        ...this.configuration,
        viewDefinitionName: definitionName,
        bundleId: this.bundleCacheService.bundleId,
        layoutTemplate: toNumber(layoutTemplate) || RX_VIEW_DEFINITION.defaultLayoutTemplateId,
        paletteComponentsPredicate: (descriptor) => !values(RX_SHELL.navBar).includes(descriptor.type)
      };

      this.rxPageTitleService.set([
        this.definitionNameService.getDisplayName(definitionName),
        this.translateService.instant('com.bmc.arsys.rx.client.view-designer.title')
      ]);
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
    this.rxComponentCanDeactivateGuard.setPageComponent(null);
  }

  onViewDefinitionSaved(viewDefinitionName: string): void {
    if (this.isNewView) {
      this.router.navigate(['edit', viewDefinitionName], { relativeTo: this.activatedRoute.parent });
    }
  }

  onViewDefinitionErrorLoading(): void {
    this.router.navigate(['new', this.bundleCacheService.bundleId], { relativeTo: this.activatedRoute.parent });
  }

  onCloseDesigner(): void {
    this.router.navigate([
      RX_APPLICATION.innovationStudioBundleId,
      this.bundleCacheService.bundleId,
      'view-definitions'
    ]);
  }

  canDeactivate(): boolean {
    return this.viewDesignerComponent?.canDeactivate() ?? true;
  }

  confirmDeactivation(): Promise<boolean> {
    return this.rxUtilityModalsService.confirmUnsavedChanges();
  }
}
